<template>
  <div v-loading="loading" element-loading-text="拼命加载中" element-loading-spinner="el-icon-loading">
    <el-row v-for="role in role_data_list" :key="role.role_id" class="mb10">
      <el-col :span="4">{{role.role_name}}</el-col>
      <el-col :span="20" class="df aic jcsa">
        <el-checkbox
          v-for="page in role.second_title_list"
          :key="page.second_title_id"
          v-model="page.isStart"
          @change="setRolePermission(role.role_id,page.second_title_id,page.isStart)"
        >{{page.second_title_name}}</el-checkbox>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  data() {
    return {
      role_data_list: [],
      loading:false
    };
  },
  mounted() {
    this.$post("permission/get_role_permission").then(res => {
      this.role_data_list = res;
    });
  },
  methods: {
    setRolePermission(role_id, second_title_id, structure) {
      this.loading=true
      this.$post("permission/set_role_permission", {
        role_id,
        second_title_id,
        structure
      }).then(res => {
        this.loading=false
      });
    }
  }
};
</script>

<style>
</style>


